.button{
    background-color: rgba(23, 26, 32, 0.8);
    height: 40px;
    width: 256px;
    margin: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
    opacity: 0.88;

}

.button>a{
    text-decoration: none;
    color: whitesmoke;
    font-size: 12px;
    
    text-transform: uppercase;

}

.button__white{
    background-color: white;
    opacity: 0.65;
}
.button__white>a{
    color: black;
    text-align: center;
}