.header {
    display: flex;
    background-color: transparent;
    background: none;
    position: fixed;
    width: 100vw;
    top: 0;
    margin: 15px;
    margin-top: 15px;
    z-index: 100;
    color: whitesmoke;
}

.header__logo{
    height: 24px;
    width: 100px;
    margin-top: 0;
}



.header__center{
    flex: 1;
    display: flex;
    justify-content: center;
}

.header__center >p {
    margin: 10px;
    text-transform: uppercase;

}

.header__right {
    display: flex;
    justify-content: flex-end;
    margin-right: 30px;

}

.header__right >p {
    margin: 10px;
    text-transform: uppercase;

}