.item{
    height: 100vh;
    width: 100vw;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    scroll-snap-align: start;
    /* overflow: hidden; */
    
}

.item__container{
    width: 40vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}


.item__text{
    margin-top: 20%;
    display: flex;
    flex-direction: column;
    font-size: 34px;
    color: black;
    align-items: center;
    flex: 1;
}

.item__textDesc {
    font-weight: 200;
    font-size: 14px;
}

.item__lowerThird{
    margin-bottom: 5%;
    display: flex;
    flex-direction: column;
}
.item__buttons{
    display: flex;
    
}
